import React, { useState, useEffect } from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components';
import { AiOutlineLoading3Quarters } from 'react-icons/ai';
import Category from './Category';

// const NewsSectionBlock = styled.div`이 안에 스타일 잡음`;
const NewsSectionBlock = styled.div`
    .row { padding-top: 80px; display: flex; flex-wrap: wrap; }
    .loadWrap { min-height: 800px; text-align: center; line-height: 800px;
        .loadicon { font-size: 100px;
            animation: loadspin infinite 5s linear;
        }
        @keyframes loadspin {
            from { transform: rotate(0deg); }
            to { transform : rotate(2turn); }
        }
    }
`;

const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    // 비동기 방식으로 가져오기
    const getNews = async () => {
        // const qeury = category === 'all' ? '' : 클릭한카테고리명;    const getNews = async
        const query = category === 'all' ? '' : `&category=${category}`;
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=30f228aafb00451d87e6625a62025d94`);
                                                                                    // &category=내용
        console.log(response);
        setArticles(response.data.articles);
        setLoading(true);
    }
    useEffect(
        () => {
            getNews()
        }, [category]
    )
    // componentDidMount와 useEffect는 동일한 기능

    if (!loading) {
        return (
            <NewsSectionBlock>
            <div className="loadWrap">
                <AiOutlineLoading3Quarters className="loadicon" />
            </div>
        </NewsSectionBlock>
        );
    } else {
        if (!articles) {
            // return null;
            return <NewsSectionBlock></NewsSectionBlock>;
        } else {
            return (
                <NewsSectionBlock>
                    <div className="row">
                        {
                            articles.map( (article) => ( <NewsItem key={article.url} article={article} /> ) )
                        }
                    </div>
                </NewsSectionBlock>
            );
        }
    }


    
    
};

export default NewsSection;