import React from 'react';
import Slider from "react-slick";
import './Homesection.scss';
import { BiCaretLeft, BiCaretRight } from 'react-icons/bi';
import 'slick-carousel/slick/slick.css';
import styled from 'styled-components';

const Article = styled.div`
    z-index: 1;
    position: relative;
    .slick-arrow {
        position:absolute; top: 50%;
        transform: translateY(-50%);
        background: none;
        padding: 10px;
        width: 80px; height: 80px;
        color: #fff;
    }
    .slick-prev { left: 100px; z-index: 99; }
    .slick-next { right: 100px; }
    .slick-dots { position: absolute; bottom: 30px; left: 50%;
        transform: translate(-50%); 
        li { display: inline-block;
            &+li { padding-left: 10px };
            button { width: 20px; height: 20px; 
                border-radius: 50%;
                background: #fff; 
                text-indent: -999999px;
                overflow: hidden; 
            }
        }
        li.slick-active { button { background: #ff0; } }
    }


    .slide { position: relative;
        height: 30vw !important;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        .text { position: absolute; left: 50%;
            top: 50%; transform: translate(-50%, -50%);
            text-align: center;
            color: #fff; 
            font-size: 50px;
            button { border-radius: 30px;
                border: 2px solid #fff;
                color: #fff;
                background: transparent; 
                font-size: 15px;
                padding: 8px 20px; 
                &:hover { background: #fff; color: #000; }
            }
        }
    }
    .slide1 { background-image: url(images/img3.jpg) };
    .slide2 { background-image: url(images/img5.jpg) };
    .slide3 { background-image: url(images/img7.jpg) };
`;


const Homesection = () => {

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay:true,
        autoplayspeed: 5000,
        prevArrow: <BiCaretLeft />,
        nextArrow: <BiCaretRight />
      };

    return (
        <div className="homesecWrap">
            <Article className="article1">
                <Slider {...settings}>
                    <div className="slide slide1">
                        <div className="text row">
                            <p>이제, 여행은 가까운 곳에서1</p>
                            <button>근처의 숙소 둘러보기1</button>
                        </div>
                    </div>
                    <div className="slide slide2">
                        <div className="text row">
                            <p>이제, 여행은 가까운 곳에서2</p>
                            <button>근처의 숙소 둘러보기2</button>
                        </div>
                    </div>
                    <div className="slide slide3">
                        <div className="text row">
                            <p>이제, 여행은 가까운 곳에서2</p>
                            <button>근처의 숙소 둘러보기2</button>
                        </div>
                    </div>
                </Slider>
            </Article>
            <article className="article2">
                <div className="row">
                    <ul>
                        <li>
                            <a href="#">
                            <img src="https://a0.muscache.com/im/pictures/be4d3ba5-08d7-4afe-95a7-f2da6453886a.jpg" alt="" />
                                <div>
                                    <strong>부산</strong>
                                    <p>차로 4.5시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                            <img src="https://a0.muscache.com/im/pictures/7253e011-7c22-48fd-b75d-d0da35372397.jpg" alt="" />
                                <div>
                                    <strong>속초시</strong>
                                    <p>차로 3시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                            <img src="https://a0.muscache.com/im/pictures/52e8083e-2de2-446d-a860-534eab250541.jpg" alt="" />
                                <div>
                                    <strong>완주군</strong>
                                    <p>차로 2.5시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                            <img src="https://a0.muscache.com/im/pictures/20e74de0-0eb8-4fca-afb8-b111875acdf5.jpg" alt="" />
                                <div>
                                    <strong>양양군</strong>
                                    <p>차로 3시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                            <img src="https://a0.muscache.com/im/pictures/e639b7ab-aee3-48ee-9743-216684a51319.jpg" alt="" />
                                <div>
                                    <strong>전주시</strong>
                                    <p>차로 2.5시간 거리</p>
                                </div>
                            </a>
                        </li>
                        <li>
                            <a href="#">
                            <img src="https://a0.muscache.com/im/pictures/ca3737ef-0faf-46ba-b055-b4a2d99e2cea.jpg" alt="" />
                                <div>
                                    <strong>강릉시</strong>
                                    <p>차로 3시간 거리</p>
                                </div>
                            </a>
                        </li>
                    </ul>
                    
                    <div className="lifeStyle">
                        <h2>어디에서나, 여행은 살아보는거야!</h2>
                        <ul>
                            <li>
                                <a href="#">
                                    <img src="https://a0.muscache.com/im/pictures/a0316ecb-e49b-4b3a-b6b6-c2876b820e8c.jpg" alt="" />
                                </a>
                                <strong>집 전체</strong>
                            </li>
                            <li>
                                <a href="#">
                                    <img src="https://a0.muscache.com/im/pictures/ff69ac49-64e7-4f4a-ae2b-ee01163d0790.jpg" alt="" />
                                </a>
                                <strong>통나무집 및 전원주택</strong>
                            </li>
                            <li>
                                <a href="#">
                                    <img src="https://a0.muscache.com/im/pictures/ce6814ba-ed53-4d6e-b8f8-c0bbcf821011.jpg" alt="" />
                                </a>
                                <strong>독특한 공간</strong>
                            </li>
                        </ul>
                    </div>
                </div>
            </article>
            <article className="article3">
                <div className="row">
                    <div>
                        <img src="images/img8.jpg" alt="" />
                        <p>상하이 샤오롱바오 만드는 법 배우기</p>
                    </div>
                    <div className="right">
                        <div className="rightFst">
                            <div>
                                <img src="images/img4.jpg" alt="" />
                                <p>그라피티와 거리 예술에 반영된 여성상 탐구</p>
                            </div>
                            <div>
                                <img src="images/img6.jpg" alt="" />
                                <p>뉴욕 마술사와 백스테이지 구경하기</p>
                            </div>
                        </div>
                        <div>
                            <img src="images/img5.jpg" alt="" />
                            <p>수제 칵테일을 마시며 젠더에 관해 토론하기</p>
                        </div>
                    </div>
                </div>
            </article>
        </div>
    );
};

export default Homesection;