import React from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';

const RoadMapBlock = styled.div`
    padding-top: 80px;
    border-top: 80px solid orange;
`;

const RoadMap = () => {
    return (
        <RoadMapBlock>
            <Header />
            <div className="row" dangerouslySetInnerHTML={{ __html:`<iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3241.2983918879!2d139.76252805045175!3d35.66965403811781!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x60188be65225f723%3A0x12d9a0d03be901a9!2sStarbucks%20Coffee%20Ginza%20Six!5e0!3m2!1sko!2skr!4v1606966701864!5m2!1sko!2skr" width="100%" height="500" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>` }} />
            <Footer />
        </RoadMapBlock>
    );
};

export default RoadMap;